<template>
    <div class="payCharge">
        <el-form :model="applyForm" :show-message="false">
            <div class="content">
                <div class="money-box">
                    <el-form-item prop="money">
                        <div class="money-item"><p class="p">{{$L('填写充值金额')}}：</p><el-input size="small" v-model="applyForm.money" :placeholder="$L('请输入充值金额')" class="money"></el-input></div>
                    </el-form-item>
                    <div class="money-item"><p class="p">{{$L('会员备注')}}：</p><el-input maxlength="500" :rows="5" type="textarea" resize="none" v-model="applyForm.remarks" placeholder="" class="remarks"></el-input></div>
                </div>
                <p class="title">{{$L('选择支付方式')}}</p>
                <span class="font-grey" v-if="payFeeCount"> {{$L('手续费')}}：{{ payFee }}</span>
                <ul class="pay-methods" v-if="tableData && tableData.hasOwnProperty('accountList') && tableData.accountList.length > 0">
                    <li class="pay-li" v-for="(item,index) in tableData.accountList" :key="index">
                        <el-radio-group v-model="applyForm.radio" @change="selectFunc(item.payId,item.payFee)">
                            <el-radio :label="item.payId">
                                <img :src="item.img" alt="">
                            </el-radio>
                        </el-radio-group>
                    </li>
                </ul>
                <div class="tips" v-else>
                    {{ $L('暂无充值方式！请联系我司！') }}
                </div>
                <div class="bottom">
                    <el-form-item>
                        <el-button type="primary" size="medium" @click="applyFunc" :disabled="!(tableData.hasOwnProperty('accountList') && tableData.accountList.length > 0)">{{$L('立即充值')}}</el-button>
                    </el-form-item>
                </div>
            </div>
        </el-form>
    </div>
</template>

<script>
    export default {
        name: "payCharge",
        data() {
            return {
                loading: true,
                errorType: 'error',
                errorTitle: this.$L('出错了'),
                errorTips: null,
                errorShow: false,
                payFee: 0,
                payFeeCount:false,
                tableData:{},
                applyForm:{
                    money: '',
                    remarks:'',
                    radio: 0,
                }
            }
        },
        created () {
            this.getData();
        },
        methods : {
            showError(title, tips, type) {
                if (type === 'warning') {
                    this.errorType = 'warning';
                } else {
                    this.errorType = 'error';
                }
                if (title && title !== '') {
                    this.errorTitle = title;
                } else {
                    this.errorTitle = this.$L('出错了');
                }
                if (tips && tips !== '') {
                    this.errorTips = tips;
                } else {
                    this.errorTips = null;
                }
                this.loading = false;
                this.errorShow = true;
            },
            backFunc(){
                this.$router.back(-1);
            },
            getData() {
                const _self = this;
                _self.loading = true;
                _self.$api('paymentOnline', {type:'mobile'} , function (reData) {
                    if (reData.flag === 1) {
                        _self.tableData = reData.data;
                        //把后台传过来对象数据转成数组
                        let arr = [];
                        for (let i in reData.data.accountList) {
                        	arr.push(reData.data.accountList[i]);
                        }
                        _self.tableData.accountList = arr;
                        _self.applyForm.radio = _self.tableData.accountList[0]['payId'];
                        if(_self.tableData.accountList[0]['payFee'] !== "0" ){
                            _self.payFeeCount = true;
                            _self.payFee = _self.tableData.accountList[0]['payFee'];
                        } else {
                            _self.payFeeCount = false;
                        }
                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('取消'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$gotoWebHome();
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.$router.push({name: 'home'});
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData();
                    }).catch(action => {
                        _self.$router.push({name: 'home'});
                    });
                });
            },
            selectFunc(id,fee){
                const _self = this;
                _self.radio = id;
                if(fee !== "0" ){
                    _self.payFeeCount = true;
                    _self.payFee = fee;
                } else {
                    _self.payFeeCount = false;
                }
            },
            applyFunc(){
                const _self = this;
                if (Number(_self.applyForm.money) && _self.applyForm.radio) {
                    _self.submitFunc();
                } else {
                    if (!Number(_self.applyForm.money)) {
                        _self.$message({
                            type: 'error',
                            message: _self.$L('请填写正确的充值金额或者充值金额不能为空!')
                        });
                    }
                    if (!_self.applyForm.radio) {
                        _self.$message({
                            type: 'error',
                            message: _self.$L('支付方式不能为空!')
                        });
                    }

                }
            },
            submitFunc(){
                const _self = this;
                _self.loading = true;
                let arr = {
                    amount: _self.applyForm.money,
                    remarks: _self.applyForm.remarks,
                    payId: _self.applyForm.radio,
                }
                _self.$api('checkoutPay', arr , function (reData) {
                    if (reData.flag === 1) {
                        _self.$router.push({
                            name:'payConfirm',
                            query:{accountId: reData.data.accountId},
                        });
                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('取消'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$gotoWebHome();
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.$router.push({name: 'home'});
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData();
                    }).catch(action => {
                        _self.$router.push({name: 'home'});
                    });
                });
            }
        }
    }
</script>

<style lang="scss" scoped>
    .payCharge{
        font-size: 14px;
        background: #fff;
        .content{
            .title{
                font-size: 16px;
                padding-left: 15px;
                line-height: 50px;
                display: inline-block;
            }
            .font-grey{
                padding-left: 20px;
                color: #999;
            }
            .money-box{
                padding: 0 15px;
                .money-item{
                    padding: 10px 0;
                    .p{
                        margin-bottom: 10px;
                    }
                }
                .money{
                    width: 100%;
                    border-bottom: 1px solid #e1e1e1;
                    /deep/ .el-input__inner{
                        border: 0;
                    }
                }
                .remarks{
                    width: 100%;
                    vertical-align: middle;
                    font-size: 14px;
                }
            }
            .pay-methods{
                border-top: 1px solid #E1E1E1;
                margin: 0 15px;
                display: -webkit-flex;
                justify-content: flex-start;
                padding: 10px 0;
                flex-wrap: wrap;
                .pay-li{
                    width: 100%;
                    margin-bottom: 20px;
                    /deep/ .el-radio-group{
                        width: 100%;
                        .el-radio{
                            width: 100%;
                            cursor: pointer;
                            .el-radio__input{
                                float: right;
                                margin-top: 25px;
                            }
                            .el-radio__label{
                                padding-left: 0;
                                .img{
                                    vertical-align: middle;
                                    width: 80px;
                                    height: 30px;
                                }
                            }
                        }
                    }
                    &:last-child{
                        margin-bottom: 0;
                    }
                }
            }
            .tips{
                color: #999;
                padding: 0 15px;
            }
            .bottom{
                padding: 30px 15px;
                .el-button{
                    width: 100%;
                    height: 40px;
                    display: inline-block;
                }
            }
        }
        .error {
            position: absolute;
            z-index: 200;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: #FFFFFF;
            overflow: hidden;

            .error-tips {
                position: absolute;
                top: 50%;
                left: 50%;
                width: 320px;
                transform: translate(-50%, -50%);
            }
        }
    }
</style>
<style scoped>
    .el-message-box {
        width: 94% !important;
    }
</style>